@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@200;300;400;500;600;700&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Syncopate:wght@400;700&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500;600;700;800;900&display=swap");

:root {
  --color-primary: #d53f41;
  --color-dark: #626262;
  --color-darker: #464646;
  --color-light: #dbd8d6;
  --color-lighter: #f5f0ec;
  --color-secondary: #2a534bb9;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.7;
  letter-spacing: 1px;
  background-color: var(--color-lighter);
  color: var(--color-darker);
}

.wrapper {
  margin: 0 5vw;
}

section {
  padding: 10vw 0;
}

.section-title {
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  padding-bottom: 5vw;
  text-transform: lowercase;
  color: var(--color-primary);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2rem 0;
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  text-transform: lowercase;
}

ul {
  display: flex;
  gap: 3rem;
}

.links,
.blog-link {
  font-weight: 700;
}

.links a,
.blog-link a {
  color: var(--color-dark);
  position: relative;
}

.links a::after,
.blog-link a::after {
  content: "";
  width: 0%;
  height: 0.2rem;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--color-primary);
  transition: 0.5s;
}

.links a:hover::after,
.blog-link a:hover::after {
  width: 120%;
}

.logo a {
  color: var(--color-darker);
}

.min-h-100vh {
  min-height: 100vh;
}

.hero {
  font-family: "Bai Jamjuree", sans-serif;
  text-transform: uppercase;
  text-align: center;
  font-size: 7vw;
  line-height: 1.2;
  min-height: 100vh;
  color: var(--color-darker);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.antique {
  color: var(--color-light);
}
.antiques2 {
  color: var(--color-primary);
}
.features {
  display: grid;
  grid-template-columns: 35% auto;
  align-items: center;
  gap: 10rem;
}

.feature-text {
  letter-spacing: 5px;
  font-weight: 500;
}

.features img {
  width: 100%;
}

.feature-l,
.feature-r {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.feature-shutter-l,
.feature-shutter-r {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  background-color: #d53f41;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-lighter);
}

.about p {
  font-size: 3vw;
  line-height: 1.5;
}

.about p:last-child {
  margin-top: 3vw;
}

.gallery .section-title {
  margin-left: 5vw;
}

.gallery-wrapper {
  background-color: var(--color-darker);
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 10vw;
  padding: 10vw;
}

.gallery-item {
  position: relative;
  width: 50%;
}

.gallery-item-title {
  position: absolute;
  top: 10%;
  left: -50%;
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 8vw;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--color-lighter);
  z-index: 1;
  mix-blend-mode: color-dodge;
}

.gallery-item-category {
  position: absolute;
  left: 0;
  bottom: -5%;
  text-transform: uppercase;
  color: var(--color-lighter);
  letter-spacing: 10px;
  z-index: 1;
}

.gallery-item-img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.blog-section {
  display: grid;
  width: 75%;
  margin: 20px auto;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 190px);
  grid-gap: 20px;
}

.blog {
  overflow: hidden;
  border-radius: 10px;
  position: relative;
}

.blog img {
  transition: all 0.3s ease-in-out;
}

.blog:hover img {
  filter: saturate(0) brightness(0.5);
}

.blog-img {
  width: 100%;
  height: 100%;
  transition: 0.5s;
}
.blog-img img {
  width: 100%;
  height: 100%;
}
.blog:nth-child(1) {
  grid-row: 1/3;
}

.blog:nth-child(3) {
  grid-column: 3/5;
}

.blog:nth-child(5) {
  grid-row: 2/4;
  grid-column: 3/4;
}

.blog:nth-child(7) {
  grid-column: 1/3;
}

.blog:nth-child(9) {
  grid-row: 4/6;
}

.blog:nth-child(11) {
  grid-column: 3/5;
}

.blog:nth-child(13) {
  grid-column: 3/4;
  grid-row: 5/7;
}

.blog:nth-child(15) {
  grid-column: 1/3;
}

.blog-overlay {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -53px;
  background-image: linear-gradient(to bottom, transparent, black);
  padding: 29px 15px 10px;
  transition: 0.5s;
}

.blog:hover .blog-overlay {
  bottom: 0px;
  transition: 0.5s;
}

.blog-title {
  font-size: 1.9rem;
  text-transform: capitalize;
  color: var(--color-light);
  letter-spacing: 1px;
  margin-bottom: 15px;
  font-weight: 400;
}

.blog-btn {
  display: inline-block;
  padding: 15px 25px;
  color: var(--color-light);
  text-transform: uppercase;
  background: rgba(255, 255, 255, 0.1);
  text-decoration: none;
  border-radius: 50px;
  line-height: 1;
  letter-spacing: 2px;
  font-size: 1rem;
}

.not-found {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-content: flex-start;
  column-gap: 5vw;
  row-gap: 1vw;
  padding: 5vw 0;
}

.headline-1,
.headline-2 {
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 8vw;
  font-weight: 700;
  line-height: 1;
  text-transform: capitalize;
}

.img-1,
.img-2 {
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.img-1 img,
.img-2 img {
  display: block;
  width: 100%;
}

.img-2 {
  justify-self: self-end;
}

.footer {
  text-align: center;
}

.footer h1 {
  font-family: "Bodoni Moda", serif;
  font-size: 10vw;
  text-transform: lowercase;
  color: var(--color-primary);
}

@media screen and (max-width: 768px) {
  html {
    font-size: 50%;
  }

  .wrapper {
    margin: 0 2.5vw;
  }

  section {
    padding: 5vw 0;
  }

  .section-title {
    padding-bottom: 2.5vw;
  }

  .navbar {
    padding: 1rem;
    flex-direction: column;
  }

  .logo {
    order: -1;
  }

  .features {
    grid-template-columns: 1fr;
    gap: 5rem;
  }

  .gallery-item-img {
    width: 100%;
    height: 100vh;
  }
}
